.App {
  
  text-align: center;
  background-color: #232838;
  color: rgb(248, 241, 232);
}

.App-logo {

  height: 10vmin;
  min-height: 8vmin;
  pointer-events: auto;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    }
}

.logo-txt {
  font-size: calc(10px + 2vmin);
}

.nav-logo {

  display: flex;
}

.nav-bar {

  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 10vh;
  background: rgba(0, 0, 0, 0.450);
  box-shadow: 0px 0px 12px rgba(66, 156, 197, 0.750);
  
}

.links-list {

  width: auto;
  min-width: 40%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}

.nav-link {

  color: rgb(248, 241, 232);
  text-decoration: none;
}

.app-header {
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 5px;
  font-size: calc(10px + 2vmin);
  color: rgb(248, 241, 232);
  
}

.app-body {

  min-height: 80vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: rgb(248, 241, 232);

}

.main {

  display: flex;
  flex-direction: column;
  align-items: center;
 
}

.App-link {

  color: #61dafb;
}

@keyframes App-logo-spin {

  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }

}

footer {
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  color: rgb(248, 241, 232);
}